.calculator{
    border: 1px solid#cccccc;
    box-shadow: 10px 10px 30px 0px #4e4e4e;
    border-radius:20px;
    position:absolute;
    top: 50%;
    left:50%;
    transform:translate(-50%, -50%);
    width:400px;
    height: 560px;
}
.calculator > img {
    margin-top: 5px;
}
.keys{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap:10px;
    padding:10px;
    margin: auto;
}
.button{
    height:60px;
    padding:5px;
    background-color:white;
    border-radius: 3px;
    border: 1px solid #c4c4c4;
    background-color:transparent;
    font-size: 2rem;
    color: #333;
    background-color:linear-gradient(to bottom, transparent, transparent 50%, rgba(0,0,0, .04));
    box-shadow: inset 0 0 0 1px rgb(255, 255, 255,.05), inset 0 1px 0 0 rgb(255, 255, 255,.45),inset 0 -1px 0 0 rgb(255, 255, 255,.15),inset 0 1px 0 0 rgb(255, 255, 255,.3875);
    text-shadow: 0 1px rgb(255, 255, 255,.04);
}
.button:hover{
    background-color:#eaeaea;
}
.operator{
    color:#fff;
    background-color:#eebb24;
}
.clear{
    background-color:#f0595f;
    border-color:#b0353a;
    color:#fff;
}
.clear:hover{
    background-color:#f1989b;
}
.equal-sign{
    background-color:rgb(139, 180, 252);
    border-color: rgb(67, 104, 167);
    color:#fff;
}
.equal-sign:hover{
    background-color:rgb(138, 154, 202);
}